import React from 'react'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import FlipMove from 'react-flip-move';
import './list.css'
export default function List({items,deleteNow,updateState}) {
    
   
    return (
        <div>
            <FlipMove>
            {items.map((value)=>{
                return(
                    <div key={value.key} className='w-100'>  
                    <div className=' d-flex align-content-center justify-content-between w-100 mt-3 btn btn-warning'>  
                    <input onChange={(e)=>{updateState(e.target.value,value.key)}} className="invisible_input" value={value.text}/>
                    <FontAwesomeIcon onClick={()=>{deleteNow(value)}} icon="trash" className='text-dark mt-1'/>
                     </div>
                     </div>
                )
            })}</FlipMove>
        </div>
    )
}
