import React, { Component } from "react";
import List from './list/list';
import "./form.css";

export default class form extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
      currentItems: {
        text: "",
        key: ""
      }
    };
    // this.handleInput=this.handleInput.bind(this)
    // this.addItem=this.handleInput.bind(this)
    this.deleteNow=this.deleteNow.bind(this)
    this.updateState=this.updateState.bind(this)
  }
  handleInput(e) {
    this.setState({
      currentItems: {
        text: e.target.value,
        key: Date.now()
      }
    });
  }
  addItem() {
    const newItem = this.state.currentItems;
    console.log(newItem);
    if(newItem.text!==""){
        console.log(newItem.length)
        const items=[...this.state.items,newItem]
        this.setState({
            items:items,
            currentItems: {
                text: "",
                key: ""
              }
        })
    }else{
        console.log("nothing")
        alert("Please input some text")
    }
  }
  deleteNow(del){
      console.log("Hello delete")
      console.log(del);
      console.log(this.state.currentItems)
      const main=this.state.items
      console.log(main)
      console.log(del)
      let fil= main.filter(it=>it!==del)
      this.setState({
          items:fil
      })
    }
    updateState(text,key){
        let allItems=this.state.items


       let newItems= allItems.map((value)=>{
            if(value.key===key){
                console.log("Changing.......")
                return {text,key}
            }else{
                return value
            }
        })
        this.setState({
            items:newItems
        })

    }
  render() {
    return (
      <div className="form-back d-flex justify-content-center">
        <form onSubmit={(e) => e.preventDefault()} id="todo-form">
          <div className="d-flex justify-content-around w-100 mt-4">
            <input
              onChange={(e) => {
                this.handleInput(e);
              }}
              value={this.state.currentItems.text}
              className="form-control w-100 mr-3"
              placeholder="Enter text"
            />
            <button
              onClick={() => {
                this.addItem();
              }}
              className="btn btn-warning"
            >
              Add
            </button>
          </div>
          <div className='mt-4 w-100'>  
          
            <List updateState={this.updateState} deleteNow={this.deleteNow} items={this.state.items}/>
          
        </div>
        </form>
      </div>
    );
  }
}
